.post {
    box-shadow: var(--shadow);
    background-color: white;
    border-radius: var(--borderRadiusCustom);
    padding: 1.2rem;
    margin: 0 0 1rem 0;
    font-family: Roboto, serif;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 27px;
}

.headerPost {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.mainPost {
    margin: 0.5rem 0 1rem 0;
    word-wrap: break-word;
    font-family: Calibri, serif;
    font-style: normal;
    font-weight: normal;
    font-size: 27px;
    line-height: 27px;

}


.footerPost {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.userLogoPost {
    height: 40px;
    width: 40px;
    border-radius: 100%;
    margin-right: 5px;
}

.userLogoPostAnonymous {
    height: 50px;
    width: 50px;
    margin-bottom: 10px;
}

.dataPost {

}

.userNamePost {
    font-family: Rajdhani, sans-serif;
    color: var(--secondColor);
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.datePost {
    font-family: Rajdhani, sans-serif;
    color: #898989;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 17px;
    display: flex;
    align-items: center;
}

.likesPost {
    display: flex;
    align-items: center;
    margin: 0 1rem 0 0;

}

.likeCount {
    color: #EA313F;
    font-family: Roboto sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    margin: 0 0 0 0.4rem;
}

.commentsPost {
    display: flex;
    align-items: center;
    margin: 0 1rem 0 0;
}

.commentsCount {
    color: #1A9BE2;
    font-family: Roboto sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    margin: 0 0 0 0.4rem;
}

.sharePost {
    display: flex;
    align-items: center;
    margin: 0 1rem 0 0;
}

.shareCount {
    color: #1A9BE2;
    font-family: Roboto sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    margin: 0 0 0 0.4rem;
}

.likesPost, .commentsPost, .sharePost img {
    cursor: pointer;
}

.boxButtonsDots {
    display: flex;
    margin: 0 0 1rem 17rem;
    cursor: pointer;
}

.btnDotsPost {
    margin: 0 5px;
    border-radius: 10px;
    height: 8px;
    width: 8px;
    background-color: #cdcdcd;
}