.cardUser {
    background: var(--backgroundBox);
    border: 2px solid var(--backgroundColor);
    border-radius: var(--borderRadiusCustom);
    display: flex;
    justify-content: space-between;
    margin: 0 1rem 0.5rem 0;
    padding: 0.5rem;
    width: 45%;
    transition: 0.05s;
    cursor: pointer;
    text-decoration: none;
}

.cardUser:hover {
    border: 2px solid var(--generalColor);
    box-shadow: var(--shadow);
}

.userLogo {
    width: 80px;
    height: 80px;
    border-radius: var(--borderRadiusCustom);
}

.userLogoAvatar {
    width: 80px;
    height: 80px;
    border-radius: var(--borderRadiusCustom);
}

.userInformText {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.userName {
    color: var(--secondColor);
    overflow: hidden;
    text-overflow: ellipsis;
}

.userCountry {
    color: var(--grayColor);
}

.userPhrase {
    font-weight: 800;
    color: var(--secondColor);
    overflow: hidden;
    text-overflow: ellipsis;
}

.userInformText2 {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.followButton_TRUE {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 50px;
    margin: 0 0 0 20px;
    border-radius: var(--borderRadiusCustom);
    border: 2px solid var(--generalColor);
    font-size: 1.5em;
    font-weight: 600;
    color: var(--generalColor);
}

.followButton_TRUE:disabled {
    opacity: 0.5;
}

.followButton_FALSE {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 50px;
    margin: 0 0 0 20px;
    border-radius: var(--borderRadiusCustom);
    border: 2px solid var(--generalColor);
    background-color: var(--generalColor);
    font-size: 1.5em;
    font-weight: 600;
    color: white;
}

.followButton_FALSE:disabled {
    opacity: 0.5;
}