.profileInform {
    margin: 1rem 0 0 0;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    border-radius: var(--borderRadiusCustom);
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('../../../assets/walp.jpg');
    background-size: cover;
    padding: 2rem;
    min-height: 400px;
}

.profileInform:after {
    background: rgba(0, 0, 0, 1.2);
}

.containerText {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 23px;
}

.containerText a {
    text-decoration: none;
    color: white;
    transition: 0.1s;
}

.containerText a:hover {
    text-decoration: none;
    color: var(--generalColor);
}

.profileInfoLeft {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 50%;
}

.profileInfoRight {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    font-size: 20px;
    color: white;
    width: 50%;
}

.userLogo {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    cursor: pointer;
    height: 200px;
    width: 200px;
    padding: 10px 30px 10px 0;
    transition: ease-in-out 0.2s;
}

.avatarUser {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: auto;
    border-radius: 15px;
}

.changePhotoTitle input {
    height: 40px;
}

.profileInfoTextBox {
    padding: 10px 0 10px 0;
}

.userName {
    color: white;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 38px;
    margin: 0;
}

.userAboutMe {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;
}

.editStatusInput {
    font-size: 20px;
    padding: 10px;
    height: 40px;
    width: 400px;
}

.userLocation {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 23px;
}

.lookingForAJob {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
}

.userWebsite {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
}

.userWebsite a {
    text-decoration: none;
    color: white;
    transition: 0.2s;
}

.userWebsite a:hover {
    color: var(--generalColor);
}


/* input__wrapper*/

.input__wrapper {
    position: relative;
    margin: 5px 0;
    text-align: center;
}

.input__file {
    opacity: 0;
    visibility: hidden;
    position: absolute;
}

.input__file_button_text {
    line-height: 1;
}

.input__file_button {
    display: flex;
    width: 175px;
    height: 25px;
    background: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: center;
    margin: 0 auto;
    font-size: 20px;
    font-weight: 700;
    padding: 7px 10px;
    cursor: pointer;
    border-radius: var(--borderRadiusCustom);
    border: 2px solid var(--generalColor);
    color: var(--generalColor);
    transition: 0.2s;
}

.input__file_button:hover {
    background-color: var(--generalColor);
    color: white;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
}

.editProfileButton {
    display: flex;
    width: 200px;
    height: 40px;
    background: white;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    border-radius: var(--borderRadiusCustom);
    border: 2px solid var(--generalColor);
    color: var(--generalColor);
    transition: 0.2s;
}

.editProfileButton:hover {
    background-color: var(--generalColor);
    color: white;
}